.memberContainer {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    margin-right: 25px;
    border-radius: 10px;
    margin: 20px;
    align-items: center;
    justify-content: flex-start;
}

.memberContainer img {
    background-color: #000;
    margin-bottom: 10px;
}

.memberName {
    font-size: 24px;
    color: #444444;
    font-weight: bold;
    padding: 0 15px;
}

.memberDesignation {
    color: '#666666';
    font-size: 16px;
    margin-bottom: 15px;
}

.memberDesc {
    padding: 0 25px;    
    text-align: center;
    margin-bottom: 15px;
    color: #666666;
    font-size: 14px;
}

.memberContainer div {
    color: #000;
}