.teamContainer {
    width: 100%;
    height: 100vh;
    background-color: #020E13;
    display: flex;
}

.leftPanel {
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: center;
    align-items: center;
    margin: 20px;
    text-align: center;
}

.leftPanel div:nth-child(1) {
    color: #fff;
    font-size: 48px;
    font-weight: bold;
}

.leftPanel div:nth-child(2) {
    margin-top: 20px;
    width: 100%;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
}

.rightPanel {
    display: flex;
    flex-direction: column;
    flex: 3;
    justify-content: center;
    overflow: hidden;
}

.rightPanel .teamCards {
    display: flex;
    overflow-x: scroll;
    scroll-behavior: smooth;
}

.teamCardArrows {
    display: flex;
    align-items: center;
    justify-content: center;
}

.teamCardArrows img {
    margin-right: 15px;
    cursor: pointer;
    transition: 0.1s;
}

.teamCardArrows img:hover {
    transform: scale(1.2);
}

.teamCards::-webkit-scrollbar {
    background-color: transparent;
}