.servicesFrame {
    display: flex;
    position: relative;
    justify-content: space-around;
    width: 100%;
    overflow: hidden;
}


.itemImgTitle {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.item {
    display: flex;
    flex-direction: row;
    transition: 0.3s;
}

.itemNumber {
    font-size: 10em;
    color: #3693B0;
    font-weight: 700;
    transition: 0.2s;
    background: -webkit-linear-gradient(#3693B0, #fff );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #3693B0;
}

.itemImg {
    background-size: cover;
    background-repeat: no-repeat;
    transition: 0.1s;
}

.itemTitle {
    color: #fff;
    margin-top: 10px;
    font-size: 16px;
    font-family: monospace;
}


@media screen and (min-width: 0px) and (max-width: 450px) {
    .servicesFrame {
        width: 100%;
        overflow: hidden;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        overflow-x: scroll;
        bottom: 60px;
        transform: scale(0.8);
    }

    .item {
        display: flex;  
        margin-right: 35px;
        flex: 1;
        position: relative;
    }

    .itemImgTitle {
        display: flex;
        flex-direction: column;
        position: relative;
    }

    .itemTitle {
        text-align: center;
    }
}

@media only screen and (min-width: 1050px) and (max-width: 1300px) {
    .item .itemNumber {
        font-size: 7em;
    }

    .itemTitle img {
        width: 100px;
        height: 100px;
    }

    .itemTitle {
        font-size: 14px;
    }

}
