.footerContainer {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #000;
}

.footerTop {
    scroll-snap-align: start;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    padding: 0px;
    height: 60vh;
}

.footerTopHeader {
    color: #444444;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 5px;    
    align-items: flex-start;
    margin-top: 10px;
    margin-left: 20px;
}

.checkboxes {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin-left: 20px;
}

.checkbox {
    margin-right: 20px;
    display: flex;
    align-items: center;
}

.checkbox span {
    font-size: 18px;
    color: #888888;
    font-weight: 500;
}

.errorMessage {
    color: red;
    margin-left: 30px;
}

.footerTopForm {
    display: flex;
    flex: 1;
    margin: 0 10px;
}

.footerForm {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.textField {
    width: 70%;
    margin-bottom: 10px !important;
}

.submitBtn {
    margin-top: 10px;
    background-color: #C48957;
    padding: 15px 20px;
    font-size: 15px;
    font-weight: 500;
    color: #fff;
    border-radius: 10px;
    transition: 0.1s;
    cursor: pointer;
}

.submitBtn:hover {
    transform: scale(1.1);
}

.footerMap {
    position: relative;
    display: flex;
    flex: 1;
}

.footerMap iframe {
    width: 100%;
    background-position: center;
    background-color: #444444;
    z-index: 999;
    border-radius: 8px;
    overflow: hidden;
    height: 280px;
}

.address {
    border-radius: 8px;
    z-index: 998;
    min-width: 350px;
    position: absolute;
    top: 0;
    right: 0;
    margin-right: -20px;
    margin-top: -20px;
    background-color: #E5E5E5;
    height: 170%;
    max-height: 500px;
}

.address .addressDetails {
    position: absolute;
    padding: 10px 20px;
}

.addressLogo {
    display: flex;
    margin-bottom: 10px;
}

.addressLogo img {
    background-color: transparent;
    width: 40px;
    margin-right: 10px;
}

.addressLogo span {
    font-size: 24px;
    color: #0B475A
}

.contact div {
    margin-bottom: 5px;
}

.contact div img {
    background-color: transparent;
    width: 30px;
}

.contact span {
    color: #888888;
    margin-left: 5px;
}

.contact div {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}


.footerBottom {
    display: flex;
    flex-direction: column;
    height: 40vh;
}


input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
